.project {
  margin-bottom: 32px;
}

.project__block {
  width: 100%;
  display: flex;
  align-items: center;

  img {
    object-position: left;
    object-fit: contain;
    margin: auto;
    max-height: 500px;
  }

  &:nth-child(2n) {
    flex-direction: row-reverse;
    text-align: left;
  }

  &:nth-child(2n) img {
    object-position: right;
  }
}

.project__img {
  height: 100%;
  width: 50%;
  flex-shrink: 0;
  display: flex;
}

.project__text {
  padding: 16px;
  width: 100%;

  p {
    font-size: 20px;
    line-height: 2;
  }
}
