.feedback {
  margin-bottom: 16px;
}

.feedback__list {
  margin: 0 0 16px;
}

.feedback__item {
  display: flex;
  background-color: $gray-light;
  padding: 16px 8px;
  height: auto;
}

.feedback__user {
  width: 80px;
  height: 80px;
  background-color: $gray-medium;
  flex-shrink: 0;
  margin-right: 16px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid $gray-dark;

  img {
    object-fit: cover;
    object-position: center;
    width: 100%;
    height: 100%;
  }
}

.feedback__text {
  display: flex;
  flex-direction: column;

  em,
  q {
    font-size: 16px;
    line-height: 1.5;
  }
}
