.gallery {
  margin-bottom: 16px;
}

.gallery__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: -8px;
  padding: 0;
}

.gallery__item {
  width: 50%;
  padding: 8px;
  overflow: hidden;

  &--blurred {
    position: relative;

    img {
      filter: blur(3px);
    }

    p {
      display: none;
    }

    &:after {
      content: 'coming soon';
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      color: $white;
      font-size: 24px;
      line-height: 1.5;
      text-align: center;
    }
  }

  a {
    position: relative;
    display: block;
    overflow: hidden;
    height: 320px;
    padding: 0;
    transition: all 0.5s ease-in-out;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      background: linear-gradient(to top, rgba($gray-darkest, 0.7) 0%, rgba($gray-darkest, 0) 40%);
      z-index: 1;
      transition: all 0.5s ease-in-out;
    }


    &[href]:hover::before,
    &[href]:focus::before {
      opacity: 0;
    }

    img {
      height: 100%;
      object-fit: cover;
      object-position: center;
      transition: all 0.5s ease-in-out;
    }

    &[href]:hover img,
    &[href]:focus img {
      transform: scale(1.05);
    }
  }
}

.gallery__text {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  //min-height: 104px;
  z-index: 1;

  color: $white;
  font-size: 24px;
  line-height: 1.5;
  text-align: center;
  padding: 16px 32px 32px;
}
