.header {
  transition: all 0.2s ease-in-out;
  background-color: rgba($gray-medium, 1);
  padding-top: 24px;
  padding-bottom: 24px;
  margin-bottom: 16px;

  &--homepage {
    background-color: rgba($gray-medium, 0.5);
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 5;
    margin-bottom: 0;
  }

  &--scrolled {
    padding-top: 8px;
    padding-bottom: 8px;
    background-color: rgba($gray-medium, 1);
  }
}

.header__inner {
  display: flex;
  align-items: flex-start;

  @media #{$breakpointTablet} {
    align-items: center;
    position: relative;
  }

  @media #{$breakpointMobile} {
    flex-direction: column;
    align-items: flex-start;
  }
}

.header__content {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: baseline;


  @media #{$breakpointMobile} {
    margin-left: 0;
    margin-right: auto;
    flex-direction: column;
    position: relative;
    width: 100%;
  }
}

.header__contacts {
  order: 1;
  display: flex;
  justify-content: flex-end;
  align-self: flex-end;

  @media #{$breakpointMobile} {
    margin-left: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}


.header__menu {
  margin-left: auto;
  margin-right: auto;

  @media #{$breakpointTablet} {
    position: absolute;
    top: 100%;
    right: 40px;
    left: 40px;
    margin-top: 0;
  }

  @media #{$breakpointMobile} {}
}

.header__logo {
  font-size: 16px;
  padding: 0;
  flex-shrink: 0;
  font-weight: 700;

  &:hover,
  &:focus {
    text-decoration: none;
  }
}

.header__btn {
  display: none;

  @media #{$breakpointMobile} {
    display: block;
    position: absolute;
    right: 20px;
    bottom: 100%;
    transform: translateY(-50%);
  }
}
