.intro {
  position: relative;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  margin-bottom: 16px;

  background-color: $secondary;
  background-image: url('/img/intro_bg.jpg');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;


  &::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba($black, 0.2);
  }
}

.intro__inner {
  position: relative;
  z-index: 1;
  text-align: center;
  color: $white;
}

.intro__heading {
  text-shadow: 0 0 10px rgba($black, 0.3);
}

.intro__text {
  li {
    font-size: 32px;
    padding: 24px 0;
    text-shadow: 0 0 10px rgba($black, 0.3);
  }
}

.intro__btn {
  margin-top: 24px;
}
