.menu {
  @media #{$breakpointMobile} {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    left: -20px;
    top: 0;
    right: -20px;
    padding: 0 20px 32px;
    box-shadow: 4px 4px 10px #F0F0F0;

    background-color: $white;
    transform: translateX(-1000px);
    transition: all 0.3s linear;
    z-index: 7;
  }

  &--open {
    visibility: visible;
    opacity: 1;
    transform: translateX(0);
  }

  &:before {
    content: '';
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background-color: rgba($gray-light, 0.5);
    height: 0;
    z-index: 7;
    opacity: 0;
    transition: all 0.1s linear;
  }

  &--open:before {
    height: 100vh;
    opacity: 1;
  }
}

.menu__list {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: 0;
  padding: 0;

  @media #{$breakpointMobile} {
    flex-direction: column;
    justify-content: center;
  }
}

.menu__item {
  position: relative;
  list-style: none;
  margin-left: 8px;
  margin-right: 8px;

  @media #{$breakpointMobile} {
    width: 100%;
  }

  &--dd {

    .header--homepage & {
      background-color: rgba($gray-medium, 0.5);
    }

    .header--scrolled & {
      background-color: rgba($gray-medium, 1);
    }

    background-color: rgba($gray-medium, 1);
    margin: 0;
    padding: 0 8px 8px 8px;
  }


}

.menu__link {
  display: inline-block;
  color: currentColor;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;
  @include underline;

  @media #{$breakpointMobile} {
    font-size: 20px;
  }

  &--accent {
    color: $accent-dark;
    transition: all 0.15s linear;

    &:hover,
    &:focus {
      text-decoration: none;
      color: $accent;
    }
  }

  &--xl {
    padding: 8px 12px;
    font-size: 20px;
  }
}

.menu__btn {
  outline: none;

  &:hover,
  &:active {
    background-color: $gray-light;
  }
}

.menu__dd {
  position: absolute;
  top: 100%;
  left: 0;
  margin: 0;
  list-style: none;
  z-index: 1;
  visibility: hidden;
  opacity: 0;
  transition: all 0.25s ease-in-out;

  padding: 26px 0 0;

  .header--scrolled & {
    padding: 8px 0 0;
  }

  .menu__item:hover & {
    visibility: visible;
    opacity: 1;
  }
}
