.socials {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style: none;
  margin-left: -4px;
  margin-right: -4px;

  &--footer {
    margin: auto -4px;
  }
}

.socials__item {
  list-style: none;
  padding-left: 0;
  margin-left: 4px;
  margin-right: 4px;
}

.socials__link {
  display: flex;
  margin: 0;
  padding: 0;
  width: 36px;
  height: 36px;
  font-size: 16px;
  color: $gray-darkest;
  text-decoration: none;
  transition: all 0.25s linear;
  border: 1px solid currentColor;
  border-radius: 50%;
  cursor: pointer;

  @media #{$breakpointMobile} {
    font-size: 20px;
  }

  .socials--footer & {
    width: 45px;
    height: 45px;
  }

  &--fb:hover,
  &--fb:focus {
    outline: none;
    color: #3c579e;
  }

  &--insta:hover,
  &--insta:focus {
    outline: none;
    color: #f69b3a;
  }

  &--wa:focus,
  &--wa:hover {
    outline: none;
    color: #25D366;
  }

  svg {
    margin: auto;
    fill: currentColor;
  }
}
