.footer {
  background-color: $gray-medium;
}

.footer__inner {
  display: flex;

  @media #{$breakpointMobile} {
    flex-direction: column;
    margin: 0 auto;
    padding-bottom: 24px;
  }

  a,
  p {
    font-size: 18px;
    line-height: 1.5;
  }
}

.footer__pics {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;

  li {
    overflow: hidden;
    padding-right: 16px;
    height: 340px;
    width: 33.3333%;
    flex-shrink: 0;
  }

  img {
    object-position: center;
    object-fit: cover;
    min-height: 100%;
    min-width: 100%;
  }
}

.footer__block {
  padding: 32px 0;
  width: 33.3333%;
  flex-shrink: 0;

  &--clmn {
    display: flex;
    flex-direction: column;
  }

  &--l {
    flex-grow: 1;
  }

  @media #{$breakpointTablet} {
    width: 33.3333%;
  }

  @media #{$breakpointMobile} {
    width: 100%;
    padding: 24px 0 0;
  }
}

.footer__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer__social {
  padding-top: 24px;
  padding-bottom: 24px;
  margin-top: auto;
  margin-bottom: auto;
}

.footer__link {
  transition: all 0.15s linear;
  padding-top: 0;
  padding-bottom: 0;
}
