.press__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.press__item {
  width: 300px;
  margin: 16px 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.press__flat {
  outline: none;
  border: 1px solid $gray-dark;
  position: relative;
  width: 300px;
  margin: auto;
  overflow: hidden;


  img {
    width: 100%;
    height: auto;
    object-fit: contain;
    object-position: center;
  }
}

.press__link {
  position: absolute;
  top: 0%;
  left: -180%;
  width: 100%;
  height: 100%;
  display: flex;
  text-align: center;
  text-decoration: none;
  transition: all 0.3s ease-in-out;

  span {
    z-index: 1;
    margin: auto;
    color: $gray-darkest;
    text-transform: uppercase;
    padding: 4px;
    border-bottom: 1px solid $gray-darkest;
  }

  &::before {
    content: '';
    position: absolute;
    left: -50px;
    right: -50px;
    top: 0;
    bottom: 0;
    background-color: rgba($secondary, 0.9);
    transform: skew(30deg);
    z-index: 0;
  }

  .press__flat:focus &,
  .press__flat:hover & {
    outline: none;
    top: 0;
    left: 0;
    text-decoration: none;
  }
}

.press__source {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8px;
}
