.glide {
  position: relative;
  width: 100%;
  box-sizing: border-box;
}

.glide * {
  box-sizing: inherit;
}

.glide__track {
  overflow: hidden;
}

.glide__slides {
  position: relative;
  width: 100%;
  list-style: none;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  touch-action: pan-Y;
  overflow: hidden;
  padding: 0;
  white-space: nowrap;
  display: flex;
  flex-wrap: nowrap;
  will-change: transform;
}

.glide__slides--dragging {
  user-select: none;
}

.glide__slide {
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  white-space: normal;
  user-select: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
}

.glide__slide a {
  user-select: none;
  -webkit-user-drag: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.glide__arrows {
  -webkit-touch-callout: none;
  user-select: none;
}

.glide__bullets {
  -webkit-touch-callout: none;
  user-select: none;
}

.glide--rtl {
  direction: rtl;
}

// arrows

.glide__arrow {
  position: absolute;
  top: 50%;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  border: 0;
  outline: 0;
  margin-top: -20px;
  border-radius: 50%;
  background-color: rgba($black, 0.2);
  transition: all .25s ease-in-out;
  box-shadow: 0 0.5rem 4rem 0 rgba(0, 0, 0, .5);
  color: $white;
  cursor: pointer;

  &--left {
    padding-right: 6px;
    left: -50px;
  }

  &--right {
    padding-left: 6px;
    right: -50px;
  }

  svg {
    fill: currentColor;
  }

  &:hover,
  &:focus {
    background-color: rgba($black, 0.5);
  }
}


// for fadeout effect!!!
.glide--intro {

  .glide__slides {
    transform: translate3d(0, 0, 0) !important;
  }

  .glide__slide {
    position: absolute;
    top: 0;
    left: 0;
    order: 2;
    opacity: 0;
    transition: opacity 1250ms ease-in-out;
  }

  .glide__slide.glide__slide--active {
    position: relative;
    order: 1;
    opacity: 1;
    z-index: 1;
  }
}
