$scroll_color: #ffffff;
$scroll_width: 32px;
$scroll_height: 60px;
$border_width: 2px;
$wheel_size: $border_width * 3;

@keyframes mouse-scroll {
  0% {
    top: 10%;
    opacity: 1;
  }

  80% {
    opacity: 1;
  }

  100% {
    top: 50%;
    opacity: 0;
  }
}

.scroll {
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
  width: $scroll_width;
  height: $scroll_height;
  border-radius: $scroll_width/2 + $border_width * 2;
  border: $border_width solid $scroll_color;
  z-index: 1;
}

.scroll__pin {
  position: absolute;
  top: 10%;
  left: 50%;
  width: $wheel_size;
  height: $wheel_size;
  margin-left: -$wheel_size/2;
  border-radius: 50%;
  background-color: $scroll_color;
  animation: mouse-scroll 1.5s infinite;
}
