.about {
  margin-bottom: 16px;
  background-color: $gray-medium;
  padding-top: 40px;
  padding-bottom: 40px;
}

.about__article {

  &::after {
    display: block;
    content: "";
    clear: both;
  }

  img {
    width: 50%;
    height: auto;
    object-fit: cover;
    object-position: center;
    float: right;
    margin-left: 32px;
  }
}

.about__text {

  p {
    font-size: 16px;
    line-height: 1.5;
    padding: 16px 0;
  }

  h3,
  h4,
  h5 {
    font-size: 24px;
    line-height: 1.5;
    padding: 16px 0;
    margin: 0;
    text-align: left;
  }
}

.about__quote {
  font-size: 32px;
  line-height: 1.5;
  text-align: center;
  padding: 16px 0;
  margin: 0;
  font-weight: 700;
  text-transform: uppercase;
  text-align: left;
  text-shadow: 1px 1px 10px $gray-medium;
}
