.comparison {
  position: relative;
  width: 75%;
  // width: 100%;
  margin: 0 auto 32px;
}

.comparison__title {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  color: $white;
  z-index: 5;
}

.comparison__container {
  position: relative;
  display: block;
  width: 100%;
  overflow: hidden;
}

.comparison__img {

  width: 100%;
  overflow: hidden;

  &--after {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 50%;
    border-right: 4px solid $secondary;

    img {
      object-fit: cover;
      object-position: center;
    }
  }


  img {
    display: block;
    user-select: none;
    box-sizing: border-box;
    // height: auto;
    // width: 1120px;
  }
}

.comparison__input {
  outline: none;
  margin: 0;
  position: absolute;
  top: 0;
  left: -10px;
  bottom: 0px;
  height: 100%;
  width: calc(100% + 20px);
  background: transparent;

  // &::before {
  //   content: '';
  //   height: 100%;
  //   width: 5px;
  //   background: white;
  // }
}

.comparison__input,
.comparison__input::-webkit-slider-runnable-track,
.comparison__input::-webkit-slider-thumb {
  -webkit-appearance: none;
}

.comparison__input::-webkit-slider-thumb {
  width: 42px;
  height: 42px;
  border: 4px solid $secondary;
  border-radius: 100%;
  background: $accent;
  background-blend-mode: overlay;
  padding: 0;
  cursor: ew-resize;
  /* box-shadow: inset 0 -2px 5px rgba(200, 79, 153, 0.5);*/
}
