.portfolio {
  margin-bottom: 16px;
}

.portfolio__content {
  display: flex;
  justify-content: space-between;
  margin: 0 -8px;
}

.portfolio__block {
  width: 50%;
  margin: 0 8px;
  padding: 0;
  position: relative;
  background-color: $gray-medium;
  overflow: hidden;

  &:before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 1;
    background-color: rgba($black, 0.2);
    transition: all 0.25s ease-in-out;
  }

  img {
    object-position: center;
    object-fit: cover;
    min-height: 100%;
    min-width: 100%;
    transition: all 0.5s ease-in-out;
  }

  p {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    color: $white;
    text-align: center;
    font-size: 32px;
    line-height: 1.5;
    text-transform: uppercase;
  }

  &:hover:before {
    background-color: rgba($black, 0);
  }

  &:hover img {
    transform: scale(1.03);
  }
}

.portfolio__btn {
  font-size: 16px;
  margin-top: 40px;
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  color: $white;
}
