.btn {
  display: inline-block;
  text-align: center;
  color: currentColor;
  border: 2px solid currentColor;
  border-radius: 24px;
  text-transform: uppercase;
  padding: 8px 16px 10px;
  transition: all 0.5s ease-in-out;
  cursor: pointer;

  &:hover,
  &:focus {
    outline: none;
    text-decoration: none;
  }

  &--primary {
    background: rgba($gray-darkest, 0.3);

    &:hover,
    &:focus {
      background-color: rgba($black, 0.5);
    }
  }

  &--l {
    font-size: 24px;
    border-width: 2px;
  }

  &--diagonal {
    position: relative;
    z-index: 0;
    display: block;
    overflow: hidden;
    height: 60px;
    line-height: 40px;
    max-width: 320px;
    margin-left: auto;
    margin-right: auto;
    text-transform: uppercase;
    border: 1px solid currentColor;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      right: -50px;
      bottom: 0;
      left: 0;
      z-index: -1;
      border-right: 50px solid transparent;
      border-bottom: 80px solid $secondary;
      transform: translateX(-100%);
      transition: 0.5s ease-in-out;
    }

    &:hover,
    &:focus {
      text-decoration: none;
      color: $gray-darkest;
      outline-width: 0;

      &:before {
        transform: translateX(0);
      }
    }
  }

  &--mega-accent {
    font-size: 24px;
    border: none;
    @include btn-border-drawing($white, $accent-thirdly, 3px, bottom, right);
  }
}
