.survey {
  position: relative;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  margin-bottom: 16px;

  background-color: $secondary;
  background-image: url('/img/test_bg.jpg');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;


  &::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba($black, 0.2);
  }
}

.survey__inner {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.survey__block {
  width: 50%;
  padding: 32px;
  background-color: rgba($gray-medium, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;

  p {
    font-size: 20px;
    line-height: 1.5;
    margin: 0;
    padding: 0 0 16px;
  }
}

.survey__btn {
  align-self: center;
  margin-left: auto;
  margin-right: auto;
}
