.heading {
  padding: 0;
  margin: 0;
  font-weight: 300;
  display: block;



  &--s {
    font-size: 20px;

    @media #{$breakpointMobile} {
      font-size: 18px;
    }
  }

  &--m {
    font-size: 24px;

    @media #{$breakpointMobile} {
      font-size: 20px;
    }
  }

  &--l {
    font-size: 32px;
    margin-bottom: 24px;
    text-align: center;

    @media #{$breakpointMobile} {
      font-size: 28px;
    }
  }

  &--xl {
    font-size: 80px;
    font-weight: 700;
  }
}
